'use client'

import { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import supabase from '@/lib/supabaseBrowserClient'
import { useRouter } from 'next/navigation'
import EdgeTab from '@/components/edgeTab'

export default function PostPage() {
  const [jobs, setJobs] = useState<any[]>([])
  const [userEmail, setUserEmail] = useState<string | null>(null)
  const router = useRouter()

  useEffect(() => {
    const load = async () => {
      const { data } = await supabase.auth.getUser()
      const email = data.user?.email
      if (email) {
        setUserEmail(email)
        const { data: jobData } = await supabase
          .from('jobs')
          .select('*, claims(id, claimer_email)')
          .eq('poster_email', email)
          .order('created_at', { ascending: false })

        setJobs(jobData || [])
      }
    }

    load()
  }, [])

  return (
    <div className="relative min-h-screen">
      <div className="fixed inset-0 z-0 bg-apricot" />

      <motion.div
        initial={{ x: '-100%' }}
        animate={{ x: 0 }}
        exit={{ x: '100%' }}
        transition={{ duration: 0.6 }}
        className="absolute z-10 w-full h-full overflow-auto bg-cadet"
      >
        <div className="relative px-6 max-w-3xl mx-auto pt-6">
          <h1 className="text-2xl font-bold mb-6 text-center text-white">
            Your Posted Jobs
          </h1>

          {jobs.map((job) => (
            <div key={job.id} className="border p-4 rounded bg-white mb-4">
              <h3 className="text-lg font-semibold">{job.title}</h3>
              <p className="text-gray-600">{job.description}</p>
              <p className="text-sm text-gray-400 mt-1">
                {job.is_paid ? 'ðŸ’° Paid' : 'ðŸš« Unpaid'}
              </p>
              <div className="mt-4">
                <p className="text-sm font-semibold">Claimed by:</p>
                {job.claims?.length > 0 ? (
                  <ul className="list-disc list-inside text-sm">
                    {job.claims.map((claim: any) => (
                      <li key={claim.id}>{claim.claimer_email}</li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-sm text-gray-400 italic">
                    No one has claimed this job yet.
                  </p>
                )}
              </div>
            </div>
          ))}
        </div>
        
      <EdgeTab
        direction="right"
        colorClass="bg-apricot"
        label="â† Find"
        navigateTo="/find"
        textColor="text-white"
      />
    </motion.div>

    </div>
  )
}
